.ui-left-userinfo {
  padding-top: 30px;
  text-align: center;
}
.ui-left-userinfo > img {
  width: 50%;
  border-radius: 50px;
}
.ui-left-userinfo > h3 {
  font-size: 16px;
  margin: 15px 0;
}
.ui-left-userinfo > p {
  padding: 0 20px;
  text-align: left;
}
.ui-left-userinfo > .ui-left-userinfo-icon {
  padding-bottom: 20px;
}
.ui-left-userinfo > .ui-left-userinfo-icon i {
  font-size: 18px;
}
.ui-empty {
  background-color: #F4F5F5;
  height: 20px;
}
