.details-content {
  padding: 20px;
}
.details-content > .details-content-header {
  padding-left: 80px;
  position: relative;
}
.details-content > .details-content-header > img {
  position: absolute;
  left: 10px;
  top: 5px;
  width: 50px;
  border-radius: 20px;
}
.details-content > .details-content-header > h4 {
  font-size: 24px;
  margin-bottom: 0;
}
.details-content > .details-content-header > p {
  color: #909090;
}
