.articleDiv {
  padding: 0;
}
.articleList {
  background-color: #f0f2f5;
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}
.articleList .articleDiv-content {
  padding: 18px 24px;
  margin: 20px 0;
  background-color: #fff;
}
.articleList .articleDiv-content:last-child {
  margin-bottom: 0;
}
.articleList .articleDiv-content > p {
  font-size: 12px;
  margin-bottom: 5px;
}
.articleList .articleDiv-content > p > span {
  color: #b2bac2;
}
.articleList .articleDiv-content:first-child {
  margin-top: 0px;
}
.article_no_data {
  position: relative;
}
.article_no_data > div {
  position: absolute;
  top: 50%;
  margin-top: -60px;
  left: 50%;
  margin-left: -40px;
}
.article_no_data > div > p {
  text-align: center;
}
.article_no_data > div > p > i {
  font-size: 30px;
}
